<template>
  <div class="search-panel">
    <div class="container">
      <div class="search-panel__wrap">
        <div class="row">
          <InputCustom
                  :customClass="place.customClass"
                  :placeholderValue="place.placeholder"
                  :label="place.label"
                  :options="place.options"
                  @input="setPlace($event)"/>
          <SelectCustom
                  :customClass="people.customClass"
                  :placeholderValue="people.placeholder"
                  :label="people.label"
                  :options="people.options"
                  @input="setPeople($event)"/>
          <SelectCustom
                  :customClass="room.customClass"
                  :placeholderValue="room.placeholder"
                  :label="room.label"
                  :options="room.options"
                  @input="setRooms($event)"/>
        </div>
        <div class="row">
          <SelectCustom
                  :customClass="room.customClass"
                  :placeholderValue="room.placeholder"
                  :label="room.label"
                  :options="room.options"
                  @input="setRooms($event)"/>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import InputCustom from './InputCustom';
  import SelectCustom from './SelectCustom';

  export default {
    data(){
      return{
        place: {
          label: "City, country, or apartment",
          placeholder: 'Start typing...',
          customClass: 'place'
        },
        people: {
          label: "Guest",
          placeholder: 'Choose guests...',
          customClass: 'people',
          options: [1,2,3,4,5]
        },
        room: {
          label: "Room",
          placeholder: 'Choose rooms...',
          customClass: 'room',
          options: [1,2,3,4,5]
        },
        searchObject: {
          place: '',
          people: '',
          rooms: ''
        }
      }
    },
    components:{
      SelectCustom,
      InputCustom
    },
    methods:{
      setPlace(str){
        this.searchObject.place = str;
        // console.log(this.searchObject);
      },
      setPeople(str){
        this.searchObject.people = str;
        // console.log(this.searchObject);
      },
      setRooms(str){
        this.searchObject.rooms = str;
        // console.log(this.searchObject);
      }
    },
    watch:{
      searchObject(objectToSearch){
        console.log(objectToSearch);
      }
    }
  }
</script>

<style lang="scss">
  .search-panel{
    position: relative;
    margin-top: -130px;
    &__wrap{
      margin: 0 auto;
      padding: 25px 0;
      width: 100%;
      max-width: 1100px;
      -webkit-border-radius: 4px;
      -moz-border-radius: 4px;
      border-radius: 4px;
      -webkit-box-shadow: 0px 0px 4px 0px rgba(0, 0, 0, 0.45);
      box-shadow: 0px 0px 4px 0px rgba(0, 0, 0, 0.45);
      background: #fff;
      .row{
        margin: 0;
      }
      @include rwd(2000){
        max-width: 1100px;
      }
      @include rwd(1800){
        max-width: 800px;
      }
    }
  }
</style>