import axios from 'axios';

export default {
  actions: {
    setPlaces(context){
      let places;
      axios
        .get('https://restcountries.eu/rest/v2/all')
        .then(response=>{
          places=response;
          context.commit('updatePlaces', places.data);
          // console.log(places.data.name);
        })
    }
  },
  mutations: {
    updatePlaces(state, places){
      state.places = places;
    }
  },
  state: {
    places: []
  },
  getters:{
    getPlaces(state){
      return state.places;
    }
  }
}